.sandwitch-wrap {
  padding: 18px 10px;
  border-radius: 3px;
  z-index: 1001;
}
.sandwitch {
  position: relative;
  display: block;
  width: 32px;
  height: 22px;
  cursor: pointer;
}
.sandwitch--active .sandwitch__line--top {
  top: 6px;
  transform: rotate(45deg);
}
.sandwitch--active .sandwitch__line--middle {
  opacity: 0;
}
.sandwitch--active .sandwitch__line--bottom {
  top: 6px;
  transform: rotate(-45deg);
}
.sandwitch__line {
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  transition: all linear 0.1s;
  background-color: white;
}
.sandwitch__line--top {
  top: 0;
}
.sandwitch__line--middle {
  top: 10px;
}
.sandwitch__line--bottom {
  top: 20px;
}
